* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #ecf0f1;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main_div {
  background-color: #ffffff;
  width: 60vw;
  /*height: 8vh;*/
  padding: 20px;
  max-width: 0 auto;
  border-radius: 10px;
  box-shadow: 10px 10px 17px 6px rgba(0,0,0,0.28);
-webkit-box-shadow: 10px 10px 17px 6px rgba(0,0,0,0.28);
-moz-box-shadow: 10px 10px 17px 6px rgba(0,0,0,0.28);
}

.main_div h1 {
  text-align: center;
  margin-bottom: 20px;
}

.main_heading {
  background-color: #f5f6fa;
  padding: 20px;
  margin: 5px 0;
  border-radius: 10px;
}

.main_heading p {
  display: inline;
  background-color: #2f3542;
  height: 20px;
  width: 20px;
  font-size: 15px;
  font-weight: 800;
  border-radius: 100%;
  padding: 10px;
  color: #ffffff;
  margin-right: 10px;
  cursor: pointer;
}

.main_heading h3 {
  display: inline;
}

p {
  background-color: #f5cd79;
  padding: 10px;
  margin: 5px 0;
  border-radius: 10px;
}